<template>
	<view style="background-color: #f1f1f1;">
		<cu-custom bgColor="bg-gradual-blue">
			<block slot="content">首页</block>
		</cu-custom>
		<swiper class="card-swiper" style="background-color: #fff;" :class="dotStyle?'square-dot':'round-dot'"
			:indicator-dots="true" :circular="true" :autoplay="true" interval="5000" duration="500" @change="cardSwiper"
			indicator-color="#8799a3" indicator-active-color="#0081ff">
			<swiper-item v-for="(item,index) in swiperList" :key="index" :class="cardCur==index?'cur':''">
				<view class="swiper-item">
					<image :src="item.url" mode="aspectFill" v-if="item.type=='image'"></image>
					<video :src="item.url" autoplay loop muted :show-play-btn="false" :controls="false"
						objectFit="cover" v-if="item.type=='video'"></video>
				</view>
			</swiper-item>
		</swiper>

		<!-- 		<view class="cu-bar bg-white solid-bottom margin-top">
			<view class="action">
				<text class="cuIcon-title text-blue"></text>分类
			</view>
		</view> -->
		<view class="listtabfu" style="background-color: #fff;">
			<view class="listtab">
				<view class="listtabsub" v-for="(item,i) in listtab" :key="i" @tap="toshoptype(item)">
					<image :src="item.img" mode="widthFix" style="width: 80%;"></image>
					<view class="listtabtext" style="font-weight: 600;">{{item.text}}</view>
				</view>
			</view>


	<!-- 		<view class="listtab">
				<view class="listtabsub" v-for="(item,i) in listtabext" :key="i" @tap="toshoptype(item)">
					<image :src="item.img" mode="widthFix" style="width: 60%;"></image>
					<view class="listtabtext">{{item.text}}</view>
				</view>
			</view> -->
		</view>



		<view class="cu-bar bg-white solid-bottom margin-top">
			<view class="action">
				<text class="cuIcon-title text-orange"></text> 奖品信息
			</view>
		</view>
		<view class="text-xsl padding classicon" style="background-color: #f1f1f1;" v-if="arr.length == 0">
			<text class=" cuIcon-info text-blue"></text>
			<view class="textclass">没有可查看的数据</view>
		</view>
		<view v-for="(item,index) in arr" :key="index">
			<view class="cu-card article" :class="isCard?'no-card':''" @click="clicfun(item)">
				<view class="cu-item shadow">
					<!-- 					<view class="title"><view class="text-cut">{{item.title}}</view></view> -->
					<view class="content" style="padding-top: 10px;">
						<image :src="item.img" mode="aspectFill"></image>
						<view class="desc">
							<view class="text-content" style="color: #000;font-size: 1.2em;font-weight: bold;">
								{{item.title}}
							</view>
							<!-- <view class="flex">
								<view class="flex-sub bg-grey padding-sm margin-xs radius tabsub">{{item.tap01}}</view>
								<view class="flex-sub bg-grey padding-sm margin-xs radius tabsub">{{item.tap02}}</view>
								<view class="flex-twice bg-grey padding-sm margin-xs radius tabsub">{{item.tap03}}
								</view>
							</view> -->
							<view>
								<view class="cu-tag bg-green light sm round">详情：{{item.detail | ellipsis }}</view>
								<!-- <view class="cu-tag bg-orange sm ">{{item.sftjj}}</view> -->
							</view>
							<view>
							<!-- 	<view class="cu-tag bg-red light sm round">发布人：{{item.submitbody}}</view> -->
								<view class="cu-tag bg-orange light sm round">积分：{{item.jifen}}</view>
							</view>
						</view>
					</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import configData from '@/config/index.js'
	const urllocal = configData.httpUrl
	export default {
		created() {

		},
		onShow() {
			this.getshopData()
		},
		onPullDownRefresh() {
			this.getshopData()
			uni.stopPullDownRefresh();
		},
		data() {
			return {
				cardCur: 0,
				listtab: [{
					id: 1,
					text: '座位中心',
					img: '../../static/icon/center.png'
				}, {
					id: 2,
					text: '座位详情',
					img: '../../static/icon/xiangqing.png'
				}, {
					id: 4,
					text: '预约签到',
					img: '../../static/icon/qiandao.png'
				},
				{
					id: 8,
					text: '留言反馈',
					img: '../../static/icon/liuyan.png'
				}],
				// listtabext: [ {
				// 	id: 8,
				// 	text: '留言反馈',
				// 	img: '../../static/icon/huanbao.png'
				// }],
				swiperList: [{
					id: 0,
					type: 'image',
					url: "http://43.143.54.23:9090/ccb-segments/2022-11/02/534fe397-144e-45f8-8b99-1ae4f4a35d9d.jpg"
				}, {
					id: 1,
					type: 'image',
					url: "http://43.143.54.23:9090/ccb-segments/2022-11/02/4127ea73-65aa-4549-a9f0-d183264e3db5.jpg"
				}, {
					id: 2,
					type: 'image',
					url: "http://43.143.54.23:9090/ccb-segments/2022-11/02/4d5a911d-9583-4925-8ad5-f213326d6915.jpg"
				}, {
					id: 3,
					type: 'image',
					url: "http://43.143.54.23:9090/ccb-segments/2022-11/02/29e23e7d-8925-4148-8b8c-ca34fb0dcf14.jpg"
				}],
				dotStyle: false,
				keyword: '',
				type: '',
				arr: [],
				CustomBar: this.CustomBar,
				isCard: false,
				positionInfo: {
					address: '',
					longitude: '', //经度
					latitude: '', //纬度
				},
				addressdata: ''
			};
		},
		methods: {
			async getshopData() {
				let that = this
				let msgobj = {}
				let resdata = await new this.Request(this.Urls.m().querywupin, msgobj).modepost()
				console.log(resdata)
				that.arr = resdata.map((val, i) => {
					val.img = urllocal + val.trans_pic.split('^^^')[0]
					return val
				})
			},
			clicfun(val) {
				console.log("123")
				uni.navigateTo({
					url: `/pages/index/shangpinduihuan?id=${val.id}&goods=${val.title}&jifen=${val.jifen}`,
					fail: (res) => {
						console.log(res) //打印错误信息
					}
				});
			},
			toshoptype(val) {
				if(val.id == 1){
					uni.navigateTo({
						url: `/pages/index/seatcenter`,
					});
				}
				if(val.id == 2){
					uni.navigateTo({
						url: `/pages/index/seatdetail`,
					});
				}
				if(val.id == 4){
					uni.navigateTo({
						url: `/pages/index/yuyueqiandao`,
					});
				}
				if(val.id == 8){
					uni.navigateTo({
						url: `/pages/index/liuyanfankui`,
					});
				}
				
				
			},
			IsCard(e) {
				this.isCard = e.detail.value
			},

			DotStyle(e) {
				this.dotStyle = e.detail.value
			},
			// cardSwiper
			cardSwiper(e) {
				this.cardCur = e.detail.current
			},
		},
		filters: {
			// 判断超出5个字显示省略号
			ellipsis(value) {
				if (!value) return ''
				if (value.length > 10) {
					return value.slice(0, 10) + '...'
				}
				return value
			},
		},
	}
</script>

<style scoped>
	.listtab {
		width: 100%;
		padding-left: 5%;
		padding-right: 5%;
		display: flex;
		justify-content: space-around;
		align-items: center;
		background-color: #fff;
		padding-top: 10px;
		padding-bottom: 10px;
	}

	.listtabsub {
		width: 18%;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
	}

	.listtabfu {
		width: 100%;
		padding-left: 10px;
		padding-right: 10px;
	}



	.text-content {
		line-height: 1;
	}

	.cu-card.article>.cu-item .content .text-content {
		height: 1em;
	}

	.cu-card.article>.cu-item .content .text-content.data-v-71bf3ec6 {
		height: 1.3em;
	}

	.padding-sm {
		padding: 3rpx;
	}

	.bg-grey {
		background-color: #fff;
		color: #b1a1a1;
	}

	.margin-xs {
		margin: 3rpx;
	}

	.tabsub {
		width: 33%;
		font-size: 0.8em;
		color: #b1a1a1;
		font-weight: 800;
	}

	.textclass {
		margin-top: 5px;
		font-weight: 600;
		font-size: 15px;
		color: darkgray;
	}

	.classicon {
		height: 70vh;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
		background-color: #fff;
		font-size: 10em;
	}
</style>